import React, {useState, useEffect} from 'react';
import {Text, View, Button, TextInput, StyleSheet, TouchableOpacity, ScrollView } from 'react-native';
import {NavigationContainer} from '@react-navigation/native';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import Constants from "expo-constants";
import * as SQLite from "expo-sqlite"; // SQLite package for local database
import { Platform } from "react-native";


// Initialize a function to open the SQLite database
function openDatabase() {
  // Handle web platform
  if (Platform.OS === "web") {
    return {
      transaction: () => {
        return {
          executeSql: () => {},
        };
      },
    };
  }
  // For mobile platforms, open the SQLite database named "db.db"
  const db = SQLite.openDatabase("db.db");
  return db;
}

// Open the SQLite database
const db = openDatabase();

// Create a Stack Navigator
const Stack = createNativeStackNavigator();

// Navigation component containing all app routes/screens
const Navigation = () => {
  return (
    <NavigationContainer>
      <Stack.Navigator>
        <Stack.Screen name="Login" component={Login} options={{title: 'Login'}} />
        <Stack.Screen name="Profile" component={ProfileScreen} />
      </Stack.Navigator>
    </NavigationContainer>
  );
};



// Login component: allows the user to enter username and password
const Login = ({navigation}) => {
  const [usernameTry, setUsername] = useState('');
  const [passwordTry, setPassword] = useState('');
  return (
    <View style={style.container}>
      <Text style={style.title}>Login in with your credentials</Text>
      <TextInput 
        placeholder="Username"
        onChangeText={newUsername => setUsername(newUsername)}
        defaultValue={usernameTry}
        style={style.input}
      >
      </TextInput>
    
      <TextInput 
        placeholder="Password"
        onChangeText={newPassword => setPassword(newPassword)}
        defaultValue={passwordTry}
        style={style.input}
      >
      </TextInput>    
      
      {/* Button to execute login, it queries the database */}
      <Button
        title="Login"
        style={style.login}
        onPress={() => {
          db.transaction((tx) => {
            tx.executeSql(
              "select * from users where name = ? and password = ?;",
              [usernameTry, passwordTry],
              (_, { rows }) => {
                if (rows.length > 0) {
                  navigation.navigate('Profile', { name: usernameTry });
                } else {
                  alert("Invalid credentials" + "\n" + "Please try again");
                }
              }
            );
          });
        }}
        
      />

    </View>
  );
};

// Profile screen: displays the logged-in user's profile
const ProfileScreen = ({navigation, route}) => {
  return <Text>This is {route.params.name}'s profile</Text>;
};

// Styles for the app's components
const style = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    padding: 20,
    backgroundColor: '#f4f4f4',
  },

  title: {
    fontSize: 24,
    marginBottom: 20,
  },
  
  input: {
    //width should look good on mobile app and on web
    width: '100%' ,
    height: 50,
    paddingHorizontal: 10,
    marginBottom: 15,
    backgroundColor: '#fff',
    borderWidth: 1,
    borderColor: '#ccc',
    borderRadius: 5,
  },

  button: {
    fontSize: 24,
    fontWeight: 'bold',
    padding: 8,
    margin: 10,
    width: 200,
  }
});

// Main app component
const App = () => {
  useEffect(() => {
    // On component mount, create the users table if it doesn't exist
    db.transaction((tx) => {
      tx.executeSql(
        "create table if not exists users (id integer primary key not null, name text, password text);"
      );
    });

    // Insert default user data into the table
    db.transaction((tx) => {
      tx.executeSql(
        "insert into users (name, password) values ('Admin', 'Admin');"
      );
      tx.executeSql(
        "insert into users (name, password) values ('User', 'User');"
      );
    });
  }, []);

  return <Navigation />;
}

// Export the main App component as the default export
export default App;